.calendar-container,
.calendar,
.calendar-header,
td,
tr {
    padding: 0;
    margin: 0;
}

.calendar-container {
    
    border: 2px solid skyblue;
 
    
}

.calendar {
    background-color: rgb(221, 177, 177);
    border-spacing: 2px;
    
    width: 50%;
}

.calendar-header {
    border-bottom: 2px dashed rgb(29, 139, 19);
    font-size: 2em;
}

.calendar-header td {
    border-spacing: 0;
    padding-left: 18px;
}

.day,
.empty-slot,
.week-day {
    text-align: center;
    height: 40px;
    
}

.week-day {
    font-size: .9em;
 

    background-color: #12CBC4
 
}

.day {
    font-size: 0.8em;
    
}

.day {
    cursor: pointer;
    border: 1px solid skyblue;
    width: 5%;
    height: 2%;
    line-height: 24px;
}

.current-day {
    background-color: #4285f4;
    width: 28px;
    height: 28px;
    line-height: 28px;
    border-radius: 2%;
    color: white;
}
.selected-day {
    background-color:yellow;
    width: 28px;
    height: 28px;
    line-height: 28px;
    border-radius: 2%;
    color: black;
}

.month-popup {
    position: absolute;
    padding: 5px;
    background: white;
    border: 2px solid skyblue;
}

.month-popup div {
    padding: 0.3em;
}

.nav-month {
    position: absolute;
    top: 0.6em;
    font-size: 0.6em;
    right:3px;
}

.prev {
}

.next {
}

.editor-year {
    max-width: 3.6em;
}

.label-month,
.label-year {
    font-size: 0.6em;
}

.label-month {
    cursor: pointer;
}
